<template>
  <v-container grid-list-xl class="choose-design-subcategory-component choose-design-component">

    <v-layout align-content-space-between column fill-height>
      <v-flex>
        <v-layout row fill-height :wrap="$vuetify.breakpoint.smAndDown">
          <v-flex >
            <v-btn flat  @click="goBack" absolute class="mt-2 hidden-sm-and-down">< Back</v-btn>
            <v-btn flat block @click="goBack" class="mt-2 hidden-md-and-up">< Back</v-btn>
          </v-flex>
          <v-flex xs12>
            <h3 class="display-2 mb-4"> Choose a Sub Category</h3>
          </v-flex>
        </v-layout>

        <v-layout :justify-center="$vuetify.breakpoint.xs" :justify-left="$vuetify.breakpoint.smAndUp" wrap>
          <v-flex xs10 sm4 md3 l3 d-block
                  v-for="item in designSubCategories" :key="item.slug"
          >
            <v-card
              class="category-card"
              color="grey lighten-3"
            >
              <v-img
                :src="item.image"
                aspect-ratio="1"
                @click="selectSubcategory(item)"
              >
                <v-layout row fill-height align-center>
                  <v-flex>
                    <span class="card-text text-uppercase headline text-center font-weight-bold">{{ item.name }}</span>
                  </v-flex>
                </v-layout>
              </v-img>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-spacer></v-spacer>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'ChooseDesignSubcategory',
  data() {
    return {};
  },
  methods: {
    goBack() {
      this.$emit('submit', 'chooseDesignCategory');
    },
    selectSubcategory(subcategory) {
      this.$store.commit('setDesignSubcategory', subcategory);
      // this.$router.push({ name: 'designs', params: {
      //   subcategory: this.currentSubcategory,
      //   name: this.currentName
      // }});
      this.$emit('submit', 'designs');
    }
  },
  computed: {
    designSubCategories() {
      return this.$store.getters.designSubcategories;
    },
    currentCategory() {
      return this.$store.state.designCategories.current &&
        this.$store.state.designCategories.current.slug;
    },
    currentSubcategory() {
      return this.$store.state.designCategories.currentSubcategory &&
        this.$store.state.designCategories.currentSubcategory.slug
    },
    currentName() {
      return this.$store.state.namesList.current &&
        this.$store.state.namesList.current.slug || this.$store.state.namesList.current.name;
    }
  },
};
</script>

